<!--  -->
<template>
<div class='goodsmb'>
    <div  class="goodsmengban">
        <img :src="canvasimg" class="cavimg" alt="">
        <img class="img1" src="@/assets/imges/poster-close.png" alt="" @click="delfn">
        <img class="img" :src="dataList.goods_img" alt="">
        <div class="content">
           <p>
               {{dataList.goods_name}}
           </p>
           <div>
{{dataList.cost_price}}
  <img :src="ewmsrc" alt="">
           </div>
           <span>
            
          
           </span>
        </div>
        
    </div>
    <div class="fll">
    <div class="dishide" >

        <canvas ></canvas>

    </div>
</div>
</div>
</template>

<script>
//jsjsjson
//import  from '';
import QRCode from "qrcode"
export default {
//import
components: {},
data() {
//
return {
 ewmsrc:"",
 canvasimg:"",
};
},
// data
computed: {
    dataList() {
        console.log(this.$store.state.goodsList)
      return this.$store.state.goodsList;
    },
},
//data
watch: {},
//
methods: {
delfn(){
    this.$emit("delflag",false)
},
async ewm(){
     let goodsUrl = location.href  //url
        console.log(QRCode.toDataURL(goodsUrl));
        var code = await QRCode.toDataURL(goodsUrl)
        console.log(code);
        this.ewmsrc=code
},
    titleAutoLine(title, canvas, x, y, lineHeight, canvasWidth, lines) {
      //   title
      //   canvas
      //x  
      //y  
      //lineHeight 
      //canvasWidth  
      //lines   
      //this.titleAutoLine(title, canvas, 20, W + 30, 30,200, 2);
      console.log(title);
      console.log(canvas);
      var ctx = canvas.getContext("2d"); //2d
      var lineWidth = 0; //
      var lastSubStringIndex = 0; //
      var beginY = y; //y

      for (let i = 0; i < title.length; i++) {
        lineWidth += ctx.measureText(title[i]).width;
        // console.log(lineWidth);
        if (lineWidth > canvasWidth) {
          if (y >= beginY + lineHeight * (lines - 1)) {
            ctx.fillText(
              title.substring(lastSubStringIndex, i - 5) + "...",
              x,
              y
            );
            return;
          } else {
            ctx.fillText(title.substring(lastSubStringIndex, i), x, y);
            y += lineHeight;
            lineWidth = 0;
            lastSubStringIndex = i;
          }
        }
      }

      // console.log(ctx.measureText(title).width);

      //ctx.fillText(title.substring(0, 23), x, y);
      //ctx.fillText(title.substring(23, 40) + "...", x, y + 30);
    },
},
beforeCreate() {}, // - 
// - this
created() {

},
beforeMount() {}, // - 
// - DOM
mounted() {
    this.ewm()
   this.$nextTick(()=>{
       let canvas=document.querySelector('canvas')//
      let ctx= canvas.getContext('2d'); //
      let H=window.screen.availHeight; // 
      let W=window.screen.availWidth;
         
    //   console.log(ctx)
      console.log(W,H)
    let devicePixelRatio = window.devicePixelRatio || 1;
 //    //pc1iphone6,7,8 2   iphone6p,7p,8p,X 3
       //canvas contextwebkitBackingStorePixelRatiosafarichrome
       //canvas
    let backingPixelRatio = ctx.webkitBackingStorePixelRatio || 1;
    let ratio = devicePixelRatio / backingPixelRatio; //
    console.log(ratio)
     
     canvas.width = W * ratio;
      canvas.height = H * ratio;
     canvas.style.width=W* ratio + "px"
     canvas.style.height=H* ratio + "px"

    
     ctx.scale(ratio, ratio);
    
      //getImageData()  putImageData() 
      var imgData = ctx.getImageData(0, 0, canvas.width, canvas.height);
      // console.log(imgData.data);
      //
      for (var i = 0; i < imgData.data.length; i += 4) {
        imgData.data[i] = 255;
        imgData.data[i + 1] = 255;
        imgData.data[i + 2] = 255;
        imgData.data[i + 3] = 255;
      }
        ctx.putImageData(imgData, 0, 0);
       function getBase64(img) {
            // console.log(img.substring(img.lastIndexOf(".") + 1).toLowerCase());
            // console.log(img);
            var canvas = document.createElement("canvas")
            var ctx = canvas.getContext("2d")
            // console.log(ctx);
            canvas.width = img.width
            canvas.height = img.height
            img.crossOrigin = "Anonymous"   //
            ctx.drawImage(img, 0, 0, img.width, img.height);
            var ext = img.src.substring(img.src.lastIndexOf(".") + 1).toLowerCase()
            var dataUrl = canvas.toDataURL("image/" + ext)
            return dataUrl
        }
      
        var image = new Image()
       
        image.crossOrigin = "Anonymous"
        image.src =this.dataList.goods_img;
        // setTimeout(() => {
        //  document.querySelector(".goodsmengban").appendChild(image)
            
        // },500);
        // setTimeout(() => {
             image.onload = function () {
            let base64 = getBase64(image)
            console.log(base64);
           var img = new Image(); //
      img.src = base64;
      // console.log(img);
      img.onload = function () {
        ctx.drawImage(img, 0, 0, W, W);
      };
        }
        // }, 2000);
        
          // 
      // 
      ctx.font = "20px Arial"; //
      ctx.fillStyle = "#333333"; //
      // ctx.textAlign = "center"; //
      // ctx.fillText(this.posterDatas.title, 20, W + 30);
      let title = this.dataList.goods_name
       console.log(title);

      this.titleAutoLine(title, canvas, 20, W + 30, 30, W - 40, 2);
        // 
      ctx.font = "30px Arial"; //
      ctx.fillStyle = "#FF4444"; //
      ctx.textAlign = "center"; //

      ctx.fillText("" + this.dataList.cost_price, W / 2, W + 130);
       //
      var img2 = new Image(); //
      img2.src = this.ewmsrc;
      // console.log(img);
      img2.onload = function () {
        ctx.drawImage(img2, 100, W + 160, W / 3, W / 3);
      };
      setTimeout(() => {
        //   console.log(666);
        console.log(canvas.toDataURL());
        this.canvasimg=canvas.toDataURL()
        // this.posterImage = canvas.toDataURL();
      }, 1000);
   })


   
},
beforeUpdate() {}, // - 
updated() {}, // - 
beforeDestroy() {}, // - 
destroyed() {}, // - 
activated() {}, //keep-alive
}
</script>
<style lang="less">

.goodsmb{
      
    .fll{
     z-index: 999;
     position: absolute;
     top: 0;
     left: 0;
     width: 100%;
     height: 100%;
       display: none;
    .dishide{
      
        
    }
    }
    position: fixed;
    top: 0;
    bottom: 0;
    left:0 ;
    right: 0;
    background-color: rgba(0,0,0,.5);
    z-index: 999;
    .goodsmengban{
        position: absolute;
        top: 15%;
        left:15%;
        width: 70%;
        height: 60%;
        background-color: #fff;
        .cavimg{
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height:100%;
      } 
        .img{
            width: 100%;
        }
        .img1{
            position: absolute;
            right: 0rem;
            top: -3rem;
            height: 3rem;
        }
        .content{
            padding: 1rem;
p{
    font-size: 1.6rem;
    color: #333;
      text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2; //
    -webkit-box-orient: vertical;
    overflow: hidden;
}
div{
    margin-top: 1rem;
     color: red;
     font-size: 1.4rem;
     line-height: 5rem;
    text-align: center;
     &::before{
         content: "";
         font-size: 1.2rem;

     }
     img{
         float: right;
         height: 7rem;
     }
}
span{
    height: 4rem;
    width: 100%;
    color: white;
    position: absolute;
    text-align: center;
    font-size: 1.6rem;
    bottom:-4rem;
    line-height: 4rem;
}
        }

    }
}
</style>